.App {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 20px;
}

.header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.item-list {
  list-style-type: none;
  padding: 0;
}

.item-list li {
  background-color: #f9f9f9;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.add-item-form {
  margin-top: 20px;
}

.input-field {
  padding: 10px;
  width: 200px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-right: 10px;
}

.add-button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-button:hover {
  background-color: #218838;
}